<template>
    <div class="login">
        <navigate title="用户登录" position="absolute"/>
        <div class="login-form">
            <div class="step1-form">
                <div class="form-item border-1px-t border-1px-b mt-20">
                    <mu-text-field v-model="form.phone" icon="perm_identity"
                                   placeholder="请输入账号/手机号码"
                                   solo full-width/>
                </div>
                <div class="form-item border-1px-b">
                    <mu-text-field v-model="form.password" icon="lock" type="password"
                                   placeholder="6-16个字符，必须是字母或数字"
                                   solo full-width/>
                </div>
                <div class="login-button">
                    <mu-button color="#fff" :disabled="validate !== '登录'"
                               @click="handlerLogin"
                               flat large>
                        {{validate}}
                    </mu-button>
                </div>
                <div class="form-button">
                    <mu-button class="forget-btn" flat @click="forgetPwd">忘记密码</mu-button>
                    <i class="line"></i>
                    <mu-button flat color="primary" @click="goRegister">我要注册</mu-button>
                </div>
            </div>
        </div>

        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import {login} from "../../api/user"
    import {mapActions} from 'vuex'

    export default {
        name: "login",
        data() {
            return {
                form: {
                    phone: '',
                    password: '',
                },
            }
        },
        computed: {
            validate() {
                const phone = /^[a-zA-Z\d]{6,20}$/.test(this.form.phone)
                const password = /^[a-zA-Z\d]{6,16}$/.test(this.form.password)

                if (!phone) {
                    return '请输入正确的账号'
                }
                if (!password) {
                    return '请输入密码'
                }

                return '登录'
            }
        },
        activated() {
            this.form = {
                phone: '',
                password: ''
            }
        },
        methods: {
            handlerLogin() {
                const data = {
                    phone: this.form.phone,
                    pwd: this.form.password
                }

                login(data).then(res => {
                    this.login(res)

                    this.$router.back()
                })
            },
            forgetPwd() {
                this.$router.push({
                    name: 'forget-password'
                })
            },
            goRegister() {
                this.$router.push({
                    name: 'register'
                })
            },
            ...mapActions({
                login: 'user/login',
            })
        },
        components: {
            Navigate
        }
    };
</script>

<style lang="stylus" scoped>
    @import '../../common/stylus/variable.styl';

    .login
        padding-top 46px
        width 100%
        height 100%
        background-color #efefef
        overflow hidden
        .login-form
            .form-item
                background-color #fff
                display flex
                &.mt-20
                    margin-top 20px
                .mu-input
                    flex 9
                .code-btn
                    position absolute
                    right 4px
                    top 6px
                    border-radius 6px
                    background #f9f9f9
                    border 1px solid #e8e8e8
            .login-button
                padding 15px
                .mu-button
                    width 100%
                    background-color #2196f3
                .mu-button.disabled
                    background-color #e6e6e6
            .form-button
                margin 0 10px 20px
                text-align center
                .mu-button
                    margin-top 20px
                    &.forget-btn
                        color #888
                .line
                    display inline-block
                    width 1px
                    height 18px
                    transform translate(0, 4px)
                    background #ccc
            .head-img
                text-align center
                padding 40px 0
</style>



